@font-face { font-family: MyriadProRegular; src: url("./fonts/MyriadPro-Regular.eot"); src: url("./fonts/MyriadPro-Regular.eot?iefix") format("embedded-opentype"), url("./fonts/MyriadPro-Regular.woff") format("woff"), url("./fonts/MyriadPro-Regular.ttf") format("truetype"); font-weight: normal; font-style: normal; font-display: swap; }

@font-face { font-family: MyriadProSemibold; src: url("./fonts/MyriadPro-Semibold.eot"); src: url("./fonts/MyriadPro-Semibold.eot?iefix") format("embedded-opentype"), url("./fonts/MyriadPro-Semibold.woff") format("woff"), url("./fonts/MyriadPro-Semibold.ttf") format("truetype"); font-weight: normal; font-style: normal; font-display: swap; }

*:focus { outline: 0; }

body { font-family: 'MyriadProRegular', sans-serif; color: #9b9b9b; }

body.is-fixed { padding-top: 70px; }

body.is-fixed header { z-index: 9; top: 0; position: absolute; height: 100vh; overflow: scroll; }

a { color: inherit; background-color: transparent; text-decoration: none; }

a:hover { transition: all 0.2s linear; color: inherit; }

.container, .container-fluid { max-width: 1280px; margin: 0 auto; }

.banner__top { min-height: 390px; margin-top: -61px; }

@media only screen and (min-width: 768px) { .banner__top { margin-top: -71px; } }

@media only screen and (min-width: 992px) { .banner__top { margin-top: -121px; } }

.banner__top a { font-family: 'MyriadProSemibold', sans-serif; font-size: 1.875rem; position: absolute; top: 50%; width: 100%; transform: translateY(-50%); text-align: center; color: #ffffff; padding: 0 15px; }

@media only screen and (min-width: 992px) { .banner__top a { padding: 0; font-size: 3.125rem; } }

.banner__top img { object-position: 50% 50%; object-fit: cover; }

.banner__bottom { position: relative; background-image: url("../img/bottom-banner.png"); background-size: cover; background-position: center center; background-repeat: no-repeat; height: 220px; }

@media only screen and (min-width: 992px) { .banner__bottom { height: 400px; } }

.banner__bottom-content { font-family: 'MyriadProSemibold', sans-serif; font-size: 1.25rem; line-height: 1.4em; text-transform: uppercase; color: #454545; background-color: #fff; text-align: center; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); padding: 25px 40px; }

@media only screen and (min-width: 992px) { .banner__bottom-content { font-size: 2rem; padding: 50px 115px; } }

.banner__bottom-content:before, .banner__bottom-content:after { content: ''; position: absolute; top: 0; width: 10px; height: 100%; background-size: 100% auto; background-repeat: y; background-image: url("../img/stamp.png"); transform: translateX(-50%); }

@media only screen and (min-width: 768px) { .banner__bottom-content:before, .banner__bottom-content:after { width: 12px; } }

@media only screen and (min-width: 992px) { .banner__bottom-content:before, .banner__bottom-content:after { width: 16px; } }

.banner__bottom-content:before { left: 0; background-position: 0 0; }

.banner__bottom-content:after { left: 100%; background-position: 100% 0; }

.main a:hover { box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2); }

.main a:hover img { opacity: 0.8; }

.main img { object-fit: cover; object-position: 50% 50%; }

.main__text { font-family: 'MyriadProSemibold', sans-serif; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); white-space: nowrap; font-size: 1.875rem; color: #fff; }

@media only screen and (min-width: 768px) { .main__text { font-size: 2.5rem; } }

@media only screen and (min-width: 992px) { .main__text { font-size: 3.126rem; } }

.about img { width: 100%; height: calc(100vw - 30px); object-fit: cover; object-position: 50% 0; }

@media only screen and (min-width: 768px) { .about img { height: auto; } }

.collections .row { position: relative; }

.collections img { width: 100%; object-fit: cover; object-position: 50% 50%; height: 210px; }

@media only screen and (min-width: 768px) { .collections img { height: 340px; } }

@media only screen and (min-width: 992px) { .collections img { height: 430px; } }

.collections img:hover { box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2); }

.collections__title { text-align: center; color: #fff; text-transform: uppercase; position: absolute; z-index: 2; top: 50%; transform: translateY(-50%); }

@media only screen and (min-width: 768px) { .collections__title { text-align: left; position: relative; transform: none; color: #4a4a4a; } }

@media only screen and (min-width: 768px) { .collections__view { height: 60px; } }

@media only screen and (min-width: 992px) { .collections__view { height: auto; } }

.collections__view a { font-size: 0.9rem; text-transform: uppercase; padding: 0.5em 1em; white-space: nowrap; color: #fff; border: 1px solid #fff; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -160%); }

@media only screen and (min-width: 768px) { .collections__view a { color: #4a4a4a; border: 1px solid #c2c2c2; transform: translate(-50%, -50%); } }

.collections__view a:hover { box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2); }

.products-list { width: 100%; }

.products-list li { position: relative; margin: 0.5em 0; }

.products-list li.has-sub:after { content: '+'; position: absolute; top: 0; right: 0; width: 10px; text-align: center; }

.products-list li.has-sub ul { max-height: 0px; transition: max-height 0.5s ease-out; overflow: hidden; }

.products-list li.has-sub ul li { margin-left: 1em; }

.products-list li.is-active { color: #4a4a4a; }

.products-list li.is-active:after { content: '-'; }

.products-list li.is-active ul { max-height: 500px; transition: max-height 1s ease-in; }

.products-list li:hover { cursor: pointer; }

.products-list a { color: #9b9b9b; text-transform: uppercase; }

.products-list a.active, .products-list a:hover { color: #4A4A4A; }

.card { display: block; box-shadow: 0 0 1px #eaeaea; }

.card:hover { box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2); }

.card:hover img { opacity: 0.7; }

.card__image img { z-index: -2; object-fit: cover; object-position: 50% 50%; }

.card__status { position: absolute; text-transform: uppercase; z-index: -1; top: 10px; left: 10px; border: 1px solid #4a4a4a; font-size: 0.75rem; line-height: 1em; padding: 2px 3px 0 3px; border-radius: 2px; }

.card__status.sale { background: #4a4a4a; color: #ffffff; }

.card__status.soldout { border-color: #4a4a4a; color: #4a4a4a; }

.card__title { color: #9B9B9B; white-space: nowrap; text-overflow: ellipsis; overflow: hidden; }

.card__price { font-family: 'MyriadProSemibold', sans-serif; color: #4A4A4A; }

.card.is-sold img, .card.is-sold .card__title, .card.is-sold .card__price { opacity: 0.5; }

.product { font-size: 0.875rem; }

.product__title { font-size: 1.125rem; color: #4a4a4a; text-transform: uppercase; }

.product__price { font-family: 'MyriadProSemibold', sans-serif; font-size: 1.125rem; color: #4a4a4a; }

.product__description { font-family: 'MyriadProSemibold', sans-serif; color: #9B9B9B; }

.product__features ul li { padding: 0.2em; }

.blog a:hover { box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2); }

.blog a:hover img { opacity: 0.8; }

.blog img { object-fit: cover; object-position: 50% 50%; }

.blog__title { max-height: 9em; overflow: hidden; text-transform: uppercase; position: absolute; top: 50%; left: 0; transform: translateY(-50%); color: #fff; padding: 0 50px; }

@media only screen and (min-width: 768px) { .blog__title { padding: 0 60px; } }

@media only screen and (min-width: 992px) { .blog__title { padding: 0 100px; font-size: 1.125rem; } }

.breadcrumb { color: #c2c2c2; display: flex; flex-wrap: wrap; padding: .75rem 0 0; list-style: none; }

.breadcrumb-item + .breadcrumb-item { padding-left: .5rem; }

.breadcrumb-item + .breadcrumb-item::before { display: inline-block; padding-right: .5rem; content: "/"; }

.breadcrumb-item + .breadcrumb-item::hover::before { text-decoration: none; }

.pagination { display: flex; padding: 2.6rem 0; }

.pagination .page-link { text-align: center; display: block; border: 1px solid transparent; border-radius: 50%; width: 2.5rem; line-height: 2.4rem; color: #9b9b9b; }

.pagination .page-link:hover { color: #4a4a4a; }

.pagination .page-link:focus { outline: 0; box-shadow: none; }

.pagination .page-link.active { border-color: #9b9b9b; }

header { padding: 18px 0 0 0; position: relative; z-index: 1; }

@media only screen and (min-width: 768px) { header { padding: 24px 0 0 0; } }

@media only screen and (min-width: 992px) { header { padding: 10px 0 30px 0; } }

header .logo { display: block; width: 92px; margin: -3px 0 0 0; }

@media only screen and (min-width: 768px) { header .logo { width: 123px; } }

@media only screen and (min-width: 992px) { header .logo { width: 204px; max-width: 100%; margin: 35px 0 0 0; } }

header a { color: #4a4a4a; text-transform: uppercase; }

header.inactive { color: #9b9b9b; }

header .nav { display: flex; flex-wrap: wrap; padding-left: 0; margin-bottom: 0; list-style: none; }

header .nav svg { vertical-align: baseline; }

header .nav li { display: block; padding: .5rem 0 .5rem 1rem; }

@media only screen and (min-width: 768px) { header .nav li { padding: .5rem 0 .5rem 2rem; } }

.nav-sub { font-size: .875rem; margin-top: -34px; }

@media only screen and (min-width: 992px) { .nav-sub { margin-top: 0; } }

.nav-sub__cart { padding-top: 2px !important; }

.nav-sub__cart svg { margin: 5px 6px 0 0; }

@media only screen and (min-width: 768px) { .nav-sub__cart svg { margin: 0 6px 0 0; } }

.nav-sub__cart a:hover { text-decoration: none !important; }

.nav-sub a { display: inline-block; }

.nav-sub a:hover { text-decoration: underline; }

.nav-sub__button { cursor: pointer; }

.nav-main a { color: #9b9b9b; }

.nav-main a.active, .nav-main a:hover { color: #4a4a4a; }

.nav-main.is-opened { position: absolute; right: 0; width: 100%; background: #ffffff; display: block !important; text-align: center; min-height: 100vh; z-index: 3; }

@media only screen and (min-width: 768px) { .nav-main.is-opened { width: 50vw; } }

.nav-main.is-opened li { border-top: 1px solid #ececec; padding: 1em 0; }

.footer { background-color: #ffffff; }

@media only screen and (min-width: 768px) { .footer { background-color: #f4f4f4; } }

.footer hr { border: .5px solid #c2c2c2; }

.footer ul { margin-bottom: 0; }

.footer__advantages { font-family: "MyriadProSemibold", sans-serif; color: #4a4a4a; }

.footer__nav a { font-size: 0.75rem; line-height: 2.4em; color: #4a4a4a; }

.footer__nav a:hover { text-decoration: underline; }

.footer__copyright { padding-top: 15px; padding-bottom: 15px; font-size: .75rem; }

.footer__copyright div, .footer__copyright a { display: block; text-align: center; }

@media only screen and (min-width: 768px) { .footer__copyright div, .footer__copyright a { text-align: right; } }

.footer__copyright svg { float: none; margin: 0; }

@media only screen and (min-width: 768px) { .footer__copyright svg { margin: 0 0 0 70px; float: right; } }

.footer__copyright-text { padding-top: 8px; }

@media only screen and (min-width: 992px) { .footer__copyright-text { padding-top: 13px; padding-bottom: 20px; } }
